body {
  margin: 0;
  overflow-x: hidden;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, p, span {
    margin: 0;
    color: #666;
}

a {
  color: #000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* navbar */
nav {
    width: 100%;
    height: 70px;
    display: flex;
    position: fixed;
    justify-content: center;
    align-items: center;
    top: 0;
    z-index: 999;
    transition: all 0.3s ease-in-out 0s;
}

.navbar-active {
    background-color: white;
}

nav::after {
    clear: both;
    content: "";
    display: block;
}

.nav-links ul {
    height: 60px;
    line-height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-links li {
    list-style: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 35px;
    font-size: 16px;
    font-weight: 400;
}

.nav-links li a {
    position: relative;
    color: #000;
    text-decoration: none;
}

.nav-links li a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    margin-bottom: 15px;
    bottom: 0;
    left: 0;
    background-color: #3E64FF;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
}

.nav-links li a:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }

/* header presentation */

header {
    width: 100%;
    height: 62em;
    background-image: linear-gradient(to left, white 0, white 50%, #ECF0FF 50%);
}

.header-presentation {
    padding-top: 25em;
    text-align: center;
}

.header-presentation h1 {
    font-size: 16px;
    font-weight: 800;
    letter-spacing: 2px;
    color: #3e64ff;
    text-transform: uppercase;
}

.header-presentation h2 {
    font-size: 60px;
    font-weight: 800;
    color: #000;
}

.header-presentation p {
    font-size: 32px;
    font-weight: 800;
    color: #000;
}

.header-presentation p .text-write-animation {
    color: #3e64ff;
}

.header-presentation p .text-write-animation:after {
    content: '|';
    margin-left: 2px;
    opacity: 1;
    -webkit-animation: pisca .7s infinite;
            animation: pisca .7s infinite;
}

/* TypeWriter */

.Typewriter__wrapper {
    color: #3e64ff;
}
