#resume .wrapper .skill-row .skill-box {
    display: flex;
    justify-content:center;
    align-items:center;
    position: relative;
    flex-direction: column;
    height: 340px;
    width: 30%;
    margin-right: 3%;
    margin-bottom: 4em;
    box-shadow:0px 10px 20px -8px #7b7474;
}

#resume .wrapper .skill-row .skill-box h2 {
    padding: 10px 0 0;
    color:#000;
    font-weight:700;
    letter-spacing:1px;
    margin-bottom: 20px;
}

#resume .wrapper .skill-row .skill-box .percent {
    width:150px;
    height:150px;
    position:relative;
    display: block;
}

#resume .wrapper .skill-row .skill-box .percent svg {
    width:150px;
    height:150px;
    position:relative;
}

.skill-row .skill-box .percent svg circle {
    width:150px;
    height:150px;
    fill:none;
    stroke-width:6;
    stroke:#000;
    -webkit-transform:translate(5px,5px);
            transform:translate(5px,5px);
    stroke-dasharray:440;
    stroke-dashoffset:440;
    stroke-linecap:round;
}

.skill-box .percent svg circle:nth-child(1) {
    stroke-dashoffset:0;
    stroke:#f3f3f3;
}

.skill-box .percent .svg-html circle:nth-child(2) {
    stroke-dashoffset:calc(440 - (440 * 95) / 100);
    stroke:#3E64FF;
}

.skill-box .percent .svg-css circle:nth-child(2) {
    stroke-dashoffset:calc(440 - (440 * 85) / 100);
    stroke:#3E64FF;
}

.skill-box .percent .svg-js circle:nth-child(2) {
    stroke-dashoffset:calc(440 - (440 * 70) / 100);
    stroke:#3E64FF;
}

.skill-box .percent .num {
    top:0;
    left:0;
    width:100%;
    height:100%;
    display:flex;
    justify-content:center;
    align-items:center;
    position:absolute;
    color:#111;
}

